body.loading {
  overflow-y: hidden;
  margin-right: var(--scrollbarWidth);

  .header {
    right: var(--scrollbarWidth);
  }
  .overlayLoader {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    position: fixed !important;
    top: 0;
    left: 0;
    z-index: 999999;
    background-color: rgba(255,255,255,.8);

    &-img {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 64px;
      height: 64px;
      border-radius: 50%;
      box-shadow: 0 0 30px rgba(79, 79, 79, 0.2);

      &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: url('../images/system/loading-rotate.png') 50% 50% no-repeat;
        animation: rotate 1.5s linear infinite;
        z-index: 1;
      }
      img {
        display: block;
        width: 18px;
      }
    }
  }
}